@import '/src/assets/styles/colors';

.root {
  min-height: 100vh;
  display: flex;
  flex: 1 0 auto;
  flex-direction: column;
  position: relative;
  z-index: 2;
  align-items: center;
  justify-content: space-between;
  padding: 20px 0;
}

.badgesList {
  position: relative;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;

  // make it 3 columns
  & > * {
    width: 33%;
  }
}
